import {
    APP_SET_ABOUT_MOUNTED,
    APP_SET_ABOUT_UNMOUNTED
} from '../actions/appActions';

const initialState = {
    aboutMounted: false,
    isLoaded: false
};


export default function(state = initialState, action) {

    switch(action.type) {
        case APP_SET_ABOUT_MOUNTED:
            return {
                ...state,
                aboutMounted: true
            };
        case APP_SET_ABOUT_UNMOUNTED:
            return {
                ...state,
                aboutMounted: false
            };
        default :
            return state;
    }
}


