import React, {PropTypes} from 'react';
import { connect } from 'react-redux';
import $ from 'jquery';

import MenuItem from './MenuItem';

import {
    menuGetItems,
    menuSetActiveItem
} from './../../actions/menuActions.js';

require('./Menu.scss');

class Menu extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    static propTypes = {
        children: PropTypes.object,
        menu: PropTypes.object,
        menuGetItems: PropTypes.func,
        menuSetActiveItem: PropTypes.func
    };

    componentWillMount() {
        if (this.props.menu.items.length < 1) this.props.menuGetItems();
    }

    componentDidMount() {
    }

    render() {
        let {items} = this.props.menu;

        return <div id="menu">
            <div className="mw">
                {items.map((a,i) => {
                    return <MenuItem menuSetActiveItem={this.props.menuSetActiveItem} item={a} history={this.props.history} key={`menuItem${a.id}`}/>;
                })}
                {(items.length < 1) ? <button onClick={this.getItems}>Get Items</button> : false}
            </div>
            {this.props.children}
        </div>;
    }

    getItems = () => {
        this.props.menuGetItems();
    };
}

const mapStateToProps = state => ({menu: state.menu});
export default connect(mapStateToProps, {
    menuGetItems,
    menuSetActiveItem
})(Menu);



