import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

require('./Windmill.scss');

class Windmill extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    componentWillMount() {
    }

    componentDidMount() {
    }

    render() {
        return <div>
            <video className="bg-video" preload="auto" autoPlay loop poster="https://s3-us-west-1.amazonaws.com/pitahproject/poster.png">
                <source src="https://s3-us-west-1.amazonaws.com/pitahproject/windmill.mp4" type="video/mp4" />
            </video>
        </div>;
    }
}

export default Windmill;



