import React, {PropTypes} from 'react';

require('./Menu.scss');

class MenuItem extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    static propTypes = {
        item: PropTypes.object,
        menuSetActiveItem: PropTypes.func,
    };

    render() {
        let {id, name, price} = this.props.item;

        return <div className="mi btn" onClick={this.handleItemClick(id)}>
            {(name) ? <h3>{name}</h3> : false}
            {(price) ? <p>{price}</p> : false}
        </div>;
    }

    handleItemClick = (slug) => {
        return () => {
            this.props.menuSetActiveItem(this.props.item);
            return this.context.router.push(`/menu/${slug}`);
        };
    }
}

export default MenuItem;



