import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

require('./Signup.scss');

class Signup extends React.Component {

    constructor() {
        super();
    }

    static propTypes = {
        userSignup: PropTypes.func
    }

    static contextTypes = {
        router: PropTypes.object
    };

    componentWillMount() {
    }

    componentDidMount() {
    }

    render() {
        return <div id="signup">
            <h1>Sign up</h1>
            <div>
                <input ref="firstName" name="firstname" type="text" placeholder="First Name" />
                <input ref="lastName" name="lastname" type="text" placeholder="Last Name" />
                <input ref="email" name="email" type="email" placeholder="Email" />
                <input ref="password" name="password" type="password" placeholder="Password" />
                <input ref="ph" name="ph" type="hidden" />
            </div>
            <button ref="signup" onClick={this.handleSignup}>Signup</button>
        </div>;
    }

    handleSignup = () => {
        let {email, firstName, lastName, password, ph} = this.refs;
        ph.value = password.value;
        let user = {password: ph.value, firstname: firstName.value, lastname: lastName.value, email: email.value};
        this.props.userSignup(user);
    };
}

export default Signup;




