import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

import {
    userLogin,
    userSignup,
    userLogout
} from './../../actions/userActions.js';

import LoginPage from './../Pages/LoginPage.js';
import Profile from './../Profile/Profile.js';

class UserPage extends React.Component {

	constructor() {
		super();
	}

    static propTypes = {
        history: PropTypes.object,
        loggedIn: PropTypes.bool,
        user: PropTypes.object,
        userLogin: PropTypes.func,
        userLogout: PropTypes.func,
        userSignup: PropTypes.func
    };

	static contextTypes = {
        router: PropTypes.object
    };

    componentWillMount() {
        
    }

    componentDidMount() {
    }

    render() {
        let {loggedIn, user} = this.props.user;
        let {userLogin, userLogout, userSignup} = this.props;

        return <div id="userPage">
            {(loggedIn) ? <Profile user={user} userLogout={userLogout} />
                : <LoginPage user={user} userSignup={userSignup} userLogin={userLogin} />}
        </div>;
    }
}

const mapStateToProps = state => ({user: state.user});
export default connect(mapStateToProps, {
    userLogin,
    userSignup,
    userLogout
})(UserPage);



