import {
    CART_PUSH_ITEMS
} from '../actions/cartActions';

const initialState = {
    items: []
};


export default function(state = initialState, action) {

    switch(action.type) {
        case CART_PUSH_ITEMS:
            return {
                ...state,
                items: action.items
            };
        default :
            return state;
    }
}



