import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

import {
    menuAddActiveItem
} from './../../actions/menuActions.js';

import classNames from 'class-names';

require('./Menu.scss');

class MenuPortal extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    static propTypes = {
        history: PropTypes.object,
        menuAddActiveItem: PropTypes.func
    }

    render() {
        return <div id="menu-portal" className="showing">
            <h1>MENU PORTAL</h1>
            <span onClick={this.handleClosePortal}>Close Portal</span>
            <span onClick={this.handleAddItem}>Add Item</span>
        </div>;
    }

    handleAddItem = () => {
        this.props.menuAddActiveItem();
        return this.context.router.push('/menu');
    };

    handleClosePortal = () => {
        return this.context.router.push('/menu');
    };
}

const mapStateToProps = state => ({menu: state.menu});
export default connect(mapStateToProps, {
    menuAddActiveItem
})(MenuPortal);



