import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

require('./Login.scss');

class Login extends React.Component {

	constructor() {
		super();
	}

    static propTypes = {
        userLogin: PropTypes.func
    };

	static contextTypes = {
        router: PropTypes.object
    };

    componentWillMount() {
    }

    componentDidMount() {
    }

    render() {
        return <div id="login">
            <h1>Login</h1>
            <div>
                <input ref="email" name="email" type="text" placeholder="Email" />
                <input ref="password" name="password" type="password" placeholder="Password" />
                <input ref="ph" name="ph" type="hidden" /> 
            </div>
            <button ref="login" onClick={this.handleLogin}>Login</button>
        </div>;
    }

    handleLogin = () => { 
        let {email, password, ph} = this.refs;
        
        ph.value = password.value;
        let user = {email: email.value, password: ph.value};
        this.props.userLogin(user);
    };
}

export default Login;




