import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

import Login from './../Login/Login.js';
import Signup from './../Login/Signup.js';

import classNames from 'class-names';

class LoginPage extends React.Component {

	constructor() {
		super();
	}

    static propTypes = {
        history: PropTypes.object,
        loggedIn: PropTypes.bool,
        user: PropTypes.object,
        userSignup: PropTypes.func,
        userLogin: PropTypes.func,
    };

    render() {
        let {user} = this.props.user, {userSignup, userLogin} = this.props;
        let loginCls = classNames('loginW', {});
        let signupCls = classNames('signupW', {});

        return <div id="loginPage">
            <div className={loginCls}>
                <Login user={user} history={history} userSignup={userSignup} userLogin={userLogin} />
            </div>
            <div className={signupCls}>
                <Signup user={user} userSignup={userSignup} userLogin={userLogin} />
            </div>
        </div>;
    }
}

export default LoginPage;



