import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

require('./HomePage.scss');

class HomePage extends React.Component {

	constructor() {
		super();
	}

    static contextTypes = {
        router: PropTypes.object
    }

    static propTypes = {
        user: PropTypes.object  
    };

    render() {
        let {loggedIn, user} = this.props.user;

        return <div id="homePage">
            <img className="hl" src="/images/logo.svg" />
            {(loggedIn) ? <span className="hwb">{`Welcome ${user.firstname} ${user.lastname}`}</span> 
                : <span className="hwbtn btn" onClick={this.handleClickLogin}>Login</span>}
        </div>;
    }

    handleClickLogin = () => {
        this.context.router.push('/login');
    }
}

const mapStateToProps = state => ({user: state.user});
export default connect(mapStateToProps, {
    
})(HomePage);



