import {
    SUBSCRIBE_SET_ACTIVE,
    SUBSCRIBE_SET_INACTIVE,
    SUBSCRIBE_SET_VALID,
    SUBSCRIBE_SET_INVALID,
    SUBSCRIBE_SET_SUBMITTED
} from '../actions/subscribeActions';

const initialState = {
    active: false,
    invalid: false,
    submitted: false
};


export default function(state = initialState, action) {

    switch(action.type) {
        case SUBSCRIBE_SET_ACTIVE:
            return {
                ...state,
                active: true
            };
        case SUBSCRIBE_SET_INACTIVE:
            return {
                ...state,
                active: false
            };
        case SUBSCRIBE_SET_VALID:
            return {
                ...state,
                invalid: false
            };
        case SUBSCRIBE_SET_INVALID:
            return {
                ...state,
                invalid: true
            };
        case SUBSCRIBE_SET_SUBMITTED:
            return {
                ...state,
                submitted: true
            };
        default :
            return state;
    }
}


