import React, {PropTypes} from 'react';

require('./Profile.scss');

class Login extends React.Component {

	constructor() {
		super();
	}

    static propTypes = {
        user: PropTypes.object,
        userLogout: PropTypes.func
    }

    componentWillMount() {
    }

    componentDidMount() {
    }

    render() {
        let {email, firstname, lastname, loggedIn} = this.props.user;

        return <div id="profile">
            <div>
                <span className="pl">First Name:</span>
                <span className="pfn">{firstname}</span>
                <span className="pl">Last Name:</span>
                <span className="pfn">{lastname}</span>
                <span className="pl">Email:</span>
                <span className="pfn">{email}</span>
                <button className="pep" onClick={this.handleEditProfile}>Edit Profile</button>
                <button className="pcp" onClick={this.handleChangePassword}>Change Password</button>
                <button className="plogout" onClick={this.handleLogout}>Logout</button>
            </div>
        </div>;
    }

    handleLogout = () => {
        this.props.userLogout();
    };
}

export default Login;




