import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

import {
    
} from './../../actions/userActions.js';

import CheckoutItem from './../Checkout/CheckoutItem';

require('./../Checkout/Checkout.scss');

class CheckoutPage extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    static propTypes = {
        cart: PropTypes.object,
    };

    componentWillMount() {
        
    }

    componentDidMount() {
    }

    render() {
        return <div id="checkoutPage">
            <h1>Checkout</h1>
            {this.props.cart.items.map((it,i) => {
                let cls = `chk-it chk-it-${i}`;
                return <CheckoutItem item={it} addClass={cls} />;
            })}
            <span className="chkModOrder btn" onClick={this.handleModifyOrder}>Modify Order</span>
        </div>;
    }

    handleModifyOrder = () => {
        this.context.router.push('/menu');
    }
}

const mapStateToProps = state => ({cart: state.cart, user: state.user,});
export default connect(mapStateToProps, {
    
})(CheckoutPage);



