import {
    USER_COMPLETE_LOGIN,
    USER_LOGOUT,
    USER_SET_ID,
    USER_SET_KEY
} from '../actions/userActions';

const initialState = {
    loggedIn: false,
    user: {
        jwt: null,
        id: null,
        firstname: null,
        lastname: null,
        email: null,
        password: null
    },
};


export default function(state = initialState, action) {

    switch(action.type) {
        case USER_COMPLETE_LOGIN:
            return {
                ...state,
                loggedIn: true,
                user: {...state.user, ...action.user}
            };
        case USER_LOGOUT:
            return {
                ...state,
                loggedIn: false,
                user: {
                    jwt: null,
                    id: null,
                    firstname: null,
                    lastname: null,
                    email: null,
                    password: null
                }
            };
        case USER_SET_ID:
            return {
                ...state,
                user: {...state.user, ...{id: action.id}}
            };
        case USER_SET_KEY:
            return {
                ...state,
                user: {...state.user, ...{jwt: action.key}}
            };
        default :
            return state;
    }
}



