import React, {PropTypes} from 'react';
import { connect } from 'react-redux';
import $ from 'jquery';

import classNames from 'class-names';

import {
	subscribeSetActive,
	subscribeSetInactive,
	subscribeSetValid,
	subscribeSetInvalid,
	subscribeSetSubmitted,
    subscribePostEmail
} from './../../actions/subscribeActions.js';

require('./SubscribeBox.scss');

class SubscribeBox extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    componentWillMount() {
    }

    componentDidMount() {
    }

    render() {
    	let {active, invalid, submitted} = this.props.subscribe;
    	let containerCls = classNames('inputContainer', {active: active, invalid: invalid, submitted: submitted});
    	let submitCls = classNames('submitBtn', {});

        return <div id="subscription-wrap">
            <span className={containerCls}>
            	<div className="pos-rel">
            		<input className="input" ref="subscribeInput" placeholder="" />
            		<span className="submitBtn" onClick={this.onClickSubmit}>
            			<span>subscribe</span>
            			<span>thanks!</span>
            			<span>bad email</span>
            		</span>
            	</div>
            </span>
        </div>;
    }

    onClickSubmit = () => {
        let {active, invalid, submitted} = this.props.subscribe;

    	if (active) {
    		// right now, validating with regex and if success submits form
            var email = this.refs.subscribeInput.value;

    		if (this.validateEmail(email)) {
                this.props.subscribePostEmail(email)
    		} else {
    			
    			this.props.subscribeSetInvalid();

    			setTimeout(() => {
    				this.props.subscribeSetValid();
    			}, 2000);
    		}
    	} else {
            this.props.subscribeSetActive();
            $(this.refs.subscribeInput).focus();
        }
    }

    validateEmail = (email) => {
    	var re = /\S+@\S+\.\S+/;
    	return re.test(email);
    }
}

const mapStateToProps = state => ({subscribe: state.subscribe});
export default connect(mapStateToProps, {
	subscribeSetActive,
	subscribeSetInactive,
	subscribeSetValid,
	subscribeSetInvalid,
	subscribeSetSubmitted,
    subscribePostEmail
})(SubscribeBox);




