import React, {PropTypes} from 'react';
import { connect } from 'react-redux';

import ShoppingCart from './../ShoppingCart/ShoppingCart';

import classNames from 'class-names';

require('./Nav.scss');

class Nav extends React.Component {

	constructor() {
		super();
	}

	static contextTypes = {
        router: PropTypes.object
    };

    static propTypes = {
        cart: PropTypes.object,
        links: PropTypes.array,
        user: PropTypes.object
    };

    componentWillMount() {
    }

    componentDidMount() {
    }

    render() {
        let {loggedIn} = this.props.user, {cart} = this.props;
        let cartAddCls = classNames('', {showing: loggedIn});
        let checkOutCls = classNames('nlchk', {showing: loggedIn && cart.items.length > 0});

        return <div id="nav">
            <div className="nlw">
                {this.props.links.map((l,i) => {
                    return <a href={`${l.link}`} onClick={this.handleNavClick(l.link)} className="nl" key={`navLink${i}`}>{l.title}</a>;
                })}
            </div>
            <div className="nrw">
                <span className="nlpro" onClick={this.handleAccountClick}>{(loggedIn) ? 'Account' : 'Login/Signup'}</span>
                <ShoppingCart addClass={cartAddCls} cart={cart} />
                <span className={checkOutCls} onClick={this.handleCheckoutClick}>Checkout</span>
            </div>
        </div>;
    }

    handleAccountClick = () => {
        if (this.props.user.loggedIn) this.context.router.push('/account');
        else this.context.router.push('/login');
    };

    handleCheckoutClick = () => {
        let {loggedIn} = this.props.user, {cart} = this.props;
        if (!loggedIn || cart.items.length < 1) return false;
        this.context.router.push('/checkout');
    }

    handleNavClick = (link) => {
        return (e) => {
            e.preventDefault();
            this.context.router.push(`${link}`);
        };
    }
}

Nav.defaultProps = {
    links: [
        {
            title: 'Home',
            link: '/'
        },
        {
            title: 'Subscribe',
            link: '/subscribe'
        },
        {
            title: 'Menu',
            link: '/menu'
        }
    ]
};

const mapStateToProps = state => ({cart: state.cart, user: state.user});
export default connect(mapStateToProps, {})(Nav);



