import {
    MENU_SET_ACTIVE_ITEM,
    MENU_RESET_ACTIVE_ITEM,

    MENU_SET_ITEMS,
    MENU_RESET_ITEMS
} from '../actions/menuActions';

const initialState = {
    activeItem: {
        category: null,
        id: null,
        name: null,
        nutrition: {},
        price: null
    },
    items: [
    ]
};


export default function(state = initialState, action) {

    switch(action.type) {
        case MENU_SET_ACTIVE_ITEM:
            return {
                ...state,
                activeItem: {
                    category: action.item.category,
                    id: action.item.id,
                    name: action.item.name,
                    nutrition: action.item.nutrition,
                    price: action.item.price
                }
            };
        case MENU_RESET_ACTIVE_ITEM:
            return {
                ...state,
                activeItem: {
                    category: null,
                    id: null,
                    name: null,
                    nutrition: {},
                    price: null
                }
            };
        case MENU_SET_ITEMS:
            return {
                ...state,
                items: action.items
            };
        case MENU_RESET_ITEMS:
            return {
                ...state,
                items: []
            };
        default :
            return state;
    }
}


